body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url("/CryptoCurrencyCover.jpg");
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.app {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.wrapper{
  display: flex;
}

.news-feed{
  background:linear-gradient(#c5cdd4 90%,whitesmoke);
  padding: 10px;
  margin: 5px;
  box-shadow: 0 3px 10px black;
  max-width: 300px;
  color: black;
}
.currency-converter{
  background:linear-gradient(#c5cdd4 90%, whitesmoke );
  padding: 10px;
  margin: 5px;
  box-shadow: 0 3px 10px black;
  text-align: center;
}
.exchange-rate{
  background-color: #afb8d8;
  padding: 20px;
  margin-top: 10px;
  box-shadow: 0 3px 10px black;
  border-radius: 10px;
}
.input-box{
  background-color: #a8caad;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 3px 10px black;
}
button{
  border: none;
  float: right;
  margin: 2px;
}
button:hover{
  background-color: rgb(226, 89, 89);
}
.title{
  color: white;
  text-shadow: #000 2px 2px 2px;
  -webkit-font-smoothing:antialiased;
}